@import '../../sass/mixins'
@import '../../sass/variables'

@keyframes icon-slide-right
    100%
        transform: translateX(0%)

@keyframes icon-slide-top
    100%
        transform: translateY(0%)

.icons
    display: flex
    gap: 1.5vw
    justify-self: start
    @include mobile-and-small
        gap: 2vw

    &--hero
        position: absolute
        z-index: 999
        flex-direction: column
        right: 5.3%
        @media not screen and (prefers-reduced-motion: reduce)
            transform: translateX(50%)
        @include mobile-and-small
            flex-direction: row
            gap: 6vw
            top: 13vw
            left: 6vw
            transform: translateY(-50%)

.link
    &:hover, &:focus
        outline: none
        & .icon
            fill: var(--color-gradient-3)
        & .wrapper
            &::before
                background-color: var(--color-font-1)
            &--hero
                &::before
                    outline-offset: 1vh
        @include mobile-and-small
            & .icon
                fill: var(--color-main-2)
            & .wrapper
                &--hero
                    &:hover, &:focus
                        &::before
                            outline-offset: 2vw

    @media not screen and (prefers-reduced-motion: reduce)
        @for $i from 1 to 4
            &:nth-child(#{$i})
                & .wrapper
                    animation-delay: $ui-slide-delay + ($i * .15s)
                    @include mobile-and-small
                        animation-delay: $ui-slide-delay + ($i * .15s)

.wrapper
    position: relative
    width: 4vh
    height: 4vh
    &::before
        @include before-after
        background-color: var(--background-color-2)
        border-radius: 50%
        width: 3vh
        height: 3vh
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        transition: background-color .4s, outline-offset .2s ease
    @include mobile-and-small
        width: 8vw
        height: 8vw
        &::before
            width: 6vw
            height: 6vw

    &--hero
        @media not screen and (prefers-reduced-motion: reduce)
            transform: translateX(10vw)
            animation: icon-slide-right 1.4s cubic-bezier(0.65, 0, 0.35, 1) forwards
        &::before
            background-color: var(--color-font-1)
            outline: .4vh solid var(--color-gradient-3)
            outline-offset: .5vh
        @include mobile-and-small
            @media not screen and (prefers-reduced-motion: reduce)
                transform: translateY(-10vh)
                animation: icon-slide-top .8s cubic-bezier(0.65, 0, 0.35, 1) forwards
            &::before
                outline-offset: 1vw
                outline: .4vh solid var(--color-main-2)

.icon
    display: block
    width: 4vh
    height: 4vh
    fill: var(--color-font-1)
    transition: fill .4s
    position: relative
    @include mobile-and-small
        width: 8vw
        height: 8vw

    &--hero
        fill: var(--background-color-2)
