@import '../../sass/mixins'

@keyframes crystal-move
    0%
        translate: 0 0
    12.5%
        translate: calc(1.3 * var(--crystal-move-distance)) calc(.7 * var(--crystal-move-distance))
    25%
        translate: calc(2 * var(--crystal-move-distance)) calc(2 * var(--crystal-move-distance))
    37.5%
        translate: calc(1.3 * var(--crystal-move-distance)) calc(3.3 * var(--crystal-move-distance))
    50%
        translate: 0 calc(4 * var(--crystal-move-distance))
    62.5%
        translate: calc(-1.3 * var(--crystal-move-distance)) calc(3.3 * var(--crystal-move-distance))
    75%
        translate: calc(-2 * var(--crystal-move-distance)) calc(2 * var(--crystal-move-distance))
    87.5%
        translate: calc(-1.3 * var(--crystal-move-distance)) calc(.7 * var(--crystal-move-distance))
    100%
        translate: 0 0

@keyframes crystal-rotate
    0%
        rotate: 0deg
    25%
        rotate: 1deg
    50%
        rotate: 0deg
    75%
        rotate: -1deg
    100%
        rotate: 0deg

.skills
    @include section
    margin-top: 10vh
    @include mobile-and-small
        margin-top: 4vh

.cards
    grid-column: 2 / 3
    grid-row: 1
    display: flex
    padding: 20vh 0
    position: relative
    justify-content: space-between
    @include mobile-and-small
        flex-direction: column
        padding: 20vh 0
        gap: 20vh
        grid-column: 1
        grid-row: 2 / 3

.icon
    height: 4vh
    width: 4vh

    &_frontend
        fill: var(--color-main-1)

    &_backend
        fill: var(--color-main-2)

    &_graphics
        fill: var(--color-main-3)

.path_svg
    position: absolute
    width: 100%
    height: 100%
    left: 0
    top: 0
    z-index: 1
    @include mobile-and-small
        grid-row: 2 / 3

.sliding_crystal
    // will-change: transform
    height: 30vh
    position: absolute
    z-index: 99
    overflow: visible
    @include mobile-and-small
        height: 40vw
        z-index: 1
    // & g
    //     will-change: transform

.floating > g
    animation: crystal-move 4s linear infinite, crystal-rotate 6s linear infinite
    @for $i from 1 to 7
        &:nth-of-type(#{$i})
            @if ($i < 6)
                animation-duration: 3 + (($i + 1) * 1s), 5 + (($i + 1) * 1s)
            // Not needed as it is same as default duration  
            // @else
            //     animation-duration: 4s, 6s
